import request from '@/utils/request'

/* 数据统计接口 */
export const getTotal = data => request({ url: '/ktdb/total/getTotal', method: 'post', data })
/* 行政区划内红线面积统计 */
export const getAreas = data => request({ url: '/ktdb/total/getAreas', method: 'post', data })
/* 界桩及登记表采集数量 */
export const getCount = data => request({ url: '/ktdb/total/getCount', method: 'post', data })
/* 红线区域用途配比 */
export const getUseArea = data => request({ url: '/ktdb/total/getUseArea', method: 'post', data })
/* 界桩数量占比 */
export const getMarkerCount = data => request({ url: '/ktdb/total/getMarkerCount', method: 'post', data })
/* 调查表数量统计 */
export const getSurveyCount = data => request({ url: '/ktdb/total/getSurveyCount', method: 'post', data })
