<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
import { getSurveyCount } from '@/api/dataCenter'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  computed: {
    legendData: function () {
      return ['生态系统状况调查', '环境质量调查', '自然背景调查', '人为活动调查']
    },
    pidData: function () {
      let Arr = []
      let eco = 0
      for (let item of this.data.ecosystemSurvey) {
        eco = eco + item.sum
      }
      Arr.push(eco)
      let eqs = 0
      for (let item of this.data.EQSurveyCount) {
        eqs = eqs + item.sum
      }
      Arr.push(eqs)
      let nbs = 0
      for (let item of this.data.NBSurveyCount) {
        nbs = nbs + item.sum
      }
      Arr.push(nbs)
      let hum = 0
      for (let item of this.data.humanActivity) {
        hum = hum + item.sum
      }
      Arr.push(hum)
      return Arr
    },
    seriesData: function () {
      let Arr = []
      for (let i in this.legendData) {
        let obj = {
          name: this.legendData[i],
          value: this.pidData[i]
        }
        Arr.push(obj)
      }
      return Arr
    }
  },
  mounted () {
    this.getSurveyCount()
  },
  methods: {
    /* 获取数据 */
    getSurveyCount () {
      getSurveyCount({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '数据调查信息统计',
          subtext: '',
          top: 20,
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 20,
          top: 50,
          data: this.legendData
        },
        series: [
          {
            type: 'pie',
            radius: '45%',
            center: ['50%', '60%'],
            data: this.seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

