<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
import { getMarkerCount } from '@/api/dataCenter'
export default {
  name: 'VerticalBar',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push(item.COM_NAME)
      }
      return Arr
    },
    marker: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push(item.markerCount)
      }
      return Arr
    },
    board: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push(item.boardCount)
      }
      return Arr
    }
  },
  mounted () {
    this.getMarkerCount()
  },
  methods: {
    /* 获取数据 */
    getMarkerCount () {
      getMarkerCount({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '勘界定标数量统计',
          subtext: '',
          textStyle: {
            fontSize: 18
          },
          left: 30,
          top: 15
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['界桩数量', '标识牌数量'],
          top: 20,
          right: 70
        },
        grid: {
          left: '5%',
          right: '11%',
          bottom: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          boundaryGap: [0, 0.01],
          name: '数量/个',
          minInterval: 1
        },
        yAxis: {
          type: 'category',
          data: this.xAxisData
        },
        series: [
          {
            name: '界桩数量',
            type: 'bar',
            data: this.marker
          },
          {
            name: '标识牌数量',
            type: 'bar',
            data: this.board
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
