<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%"  v-resize="redraw"></div>
</template>

<script>
import { getSurveyCount } from '@/api/dataCenter'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  computed: {
    xAxisData: function () {
      let dataArr = []
      let curDate = new Date()
      for (let i = 0; i < 15; i++) {
        let d = new Date(curDate.getTime() - 24 * 60 * 60 * 1000 * i)
        let preDate = (d.getMonth() + 1) + '-' + d.getDate()
        dataArr.push(preDate)
      }
      dataArr.reverse()
      return dataArr
    },
    humanActivityData: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.humanActivity) {
          if (formatChange(this.data.humanActivity[j].date) === this.xAxisData[i]) {
            d = this.data.humanActivity[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.humanActivityBeforeCount
      }
      return Arr
    },
    NBSurveyCountData: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.NBSurveyCount) {
          if (formatChange(this.data.NBSurveyCount[j].date) === this.xAxisData[i]) {
            d = this.data.NBSurveyCount[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.NBSurveyBeforeCount
      }
      return Arr
    },
    EQSurveyCountData: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.EQSurveyCount) {
          if (formatChange(this.data.EQSurveyCount[j].date) === this.xAxisData[i]) {
            d = this.data.EQSurveyCount[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.EQSurveyBeforeCount
      }
      return Arr
    },
    ecosystemSurveyData: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.ecosystemSurvey) {
          if (formatChange(this.data.ecosystemSurvey[j].date) === this.xAxisData[i]) {
            d = this.data.ecosystemSurvey[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.ecosystemSurveyBeforeCount
      }
      return Arr
    }
  },
  mounted () {
    this.getSurveyCount()
  },
  methods: {
    /* 获取数据 */
    getSurveyCount () {
      getSurveyCount({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '数据调查台账',
          left: 35,
          top: 10
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['生态系统状况调查', '环境质量调查', '自然背景调查', '人为活动调查'],
          right: 40,
          top: 40,
          bottom: 20
        },
        grid: {
          right: 60,
          top: 85,
          left: 50,
          bottom: 40
        },
        xAxis: [
          {
            type: 'category',
            data: this.xAxisData
          }
        ],
        yAxis: {
          type: 'value',
          minInterval: 1
        },
        series: [
          {
            name: '生态系统状况调查',
            type: 'line',
            data: this.ecosystemSurveyData
          },
          {
            name: '环境质量调查',
            type: 'line',
            data: this.EQSurveyCountData
          },
          {
            name: '自然背景调查',
            type: 'line',
            data: this.NBSurveyCountData
          },
          {
            name: '人为活动调查',
            type: 'line',
            data: this.humanActivityData
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

