<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%"  v-resize="redraw"></div>
</template>

<script>
import { getAreas } from '@/api/dataCenter'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  mounted () {
    this.getAreas()
  },
  computed: {
    legendData: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push(item.name)
      }
      return Arr
    },
    pidData: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push((item.area / 1000000).toFixed(2))
      }
      return Arr
      // return Arr
    },
    seriesData: function () {
      let Arr = []
      for (let i in this.legendData) {
        let obj = {
          name: this.legendData[i],
          value: this.pidData[i]
        }
        Arr.push(obj)
      }
      return Arr
    }
  },
  methods: {
    /* 获取数据 */
    getAreas () {
      getAreas({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '红线面积占比',
          subtext: '',
          top: 20,
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c}m² ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 20,
          top: 50,
          data: this.legendData
        },
        series: [
          {
            type: 'pie',
            radius: '45%',
            center: ['50%', '55%'],
            data: this.seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

