<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%"  v-resize="redraw"></div>
</template>

<script>
import { getCount } from '@/api/dataCenter'
export default {
  name: 'LindChart',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  computed: {
    xAxisData: function () {
      let dataArr = []
      let curDate = new Date()
      for (let i = 0; i < 15; i++) {
        let d = new Date(curDate.getTime() - 24 * 60 * 60 * 1000 * i)
        let preDate = (d.getMonth() + 1) + '-' + d.getDate()
        dataArr.push(preDate)
      }
      dataArr.reverse()
      return dataArr
    },
    yAxisTablet: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.markerCount) {
          if (formatChange(this.data.markerCount[j].date) === this.xAxisData[i]) {
            d = this.data.markerCount[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.beforeMarkerCount
      }
      return Arr
    },
    yAxisTabletTab: function () {
      // 将日期数据中的0过滤掉
      let formatChange = (data) => {
        data = data.split('-')
        for (let i in data) {
          data[i] = data[i].replace(/\b(0+)/gi, '')
        }
        data = data.join('-')
        return data
      }
      let Arr = []
      for (let i in this.xAxisData) {
        let d = 0
        for (let j in this.data.boardCount) {
          if (formatChange(this.data.boardCount[j].date) === this.xAxisData[i]) {
            d = this.data.boardCount[j].sum
          }
        }
        Arr.push(d)
      }
      for (let i in Arr) {
        if (i > 0) {
          Arr[i] = Arr[i] + Arr[i - 1]
        }
      }
      for (let i in Arr) {
        Arr[i] = Arr[i] + this.data.beforeBoardCount
      }
      return Arr
    }
  },
  mounted () {
    this.getCount()
  },
  methods: {
    /* 获取数据 */
    getCount () {
      getCount({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '勘界定标台账',
          left: 35,
          top: 10
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['实际界桩数量', '实际标识牌数量'],
          right: 40,
          top: 40,
          bottom: 20
        },
        grid: {
          right: 60,
          top: 85,
          left: 50,
          bottom: 40
        },
        xAxis: [
          {
            type: 'category',
            data: this.xAxisData
          }
        ],
        yAxis: {
          type: 'value',
          minInterval: 1
        },
        series: [
          {
            name: '实际界桩数量',
            type: 'line',
            data: this.yAxisTablet
          },
          {
            name: '实际标识牌数量',
            type: 'line',
            data: this.yAxisTabletTab
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

