<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
import { getAreas } from '@/api/dataCenter'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {}
      /* barData: {
        xAxisData: ['海北州', '玉树州', '西宁市', '海东市', '黄南州', '海南州', '果洛州', '海西州'],
        yAxisData: [500, 2000, 3006, 1080, 1000, 2000, 5000, 1800]
      } */
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push(item.name)
      }
      return Arr
    },
    area: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push((item.area / 1000000).toFixed(2))
      }
      return Arr
    },
    redlineSum: function () {
      let Arr = []
      for (let item of this.data) {
        Arr.push((item.redlineSum))
      }
      return Arr
    }
  },
  mounted () {
    this.getAreas()
  },
  methods: {
    /* 获取数据 */
    getAreas () {
      getAreas({
        data: {
          code: this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '红线台账',
          left: 35,
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        legend: {
          data: ['红线斑块数量', '红线面积'],
          top: 30,
          right: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          formatter: function (params) {
            return params[0].name + ':' + params[0].value + '<br>' + params[1].name + ':' + params[1].value + 'm²'
          }
        },
        xAxis: {
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '红线数量',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            name: '红线面积/m²',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '红线斑块数量',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: '#1ba9f5'
              }
            },
            data: this.redlineSum,
            barCategoryGap: '35%'
          },
          {
            name: '红线面积',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: '#19ce9b'
              }
            },
            yAxisIndex: 1,
            data: this.area,
            barCategoryGap: '35%'
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

